import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OperationEnum,
  OrderType,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../dependencies/empresaMCGDependency';
import { CLASS_NAME, ContaHistorico } from '../../../../models/dtos/mcg/contaHistorico';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'planoConta.conta',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const HistoricoExtratoPlanoContaSearchView: FC<SearchViewProps<ContaHistorico>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const empresa = globalReducer.globalParameter.empresa;
  const columns: ColumnsProps[] = [
    {
      dataField: 'planoConta.conta',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Plano Conta',
    },
    {
      dataField: 'planoConta.descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Plano Conta Descrição',
    },
    {
      dataField: 'historicoExtrato',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Histórico Extrato',
    },
  ];

  const getSearchExpression = (): SearchExpression[] => {
    return [
      {
        fieldName: 'empresa.id',
        operation: OperationEnum.EQUAL,
        value: empresa.id,
      },
    ];
  };

  return (
    <WindowComponent
      title="Histórico - Plano Contas Extratos"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMCG(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'HistoricoExtratoPlanoContaEditView', CLASS_NAME)}
        searchExpressions={getSearchExpression}
      />
    </WindowComponent>
  );
};

export default HistoricoExtratoPlanoContaSearchView;
